@import url('https://fonts.googleapis.com/css2?family=Comfortaa:wght@600&family=Inconsolata:wght@400;600&display=swap');
:root{
    --bg: #f7f7f7;
    --vermilion: #CD4631;
    --tumbleweed: #DEA47E;
    --brown-sugar: #9E6240;
    --eggshell: #F8F2DC;
    --pewter-blue: #81ADC8;
    --gray: rgba(212, 209, 209, 0.589);
    --dark-blue: #2b2d42;
    --header-font-size: 16px;
}

body{
    font-family: 'Comfortaa', cursive;
}


/* */

.login_container{
    /* padding: 2% 4%; */
    -moz-box-shadow:    inset 0 1px 0 var(--eggshell);
    -webkit-box-shadow: inset 0 1px 0 var(--eggshell);
    box-shadow:         inset 0 1px 0 var(--eggshell);
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-color: var(--bg);
    height: 90vh;
}
/* 
.to_register{
    color: #1D3557;
}

.register_link{
    text-decoration: none;
    color: #457B9D;
} */

.login_form{
    margin: 70px;
    background-color: var(--dark-blue);
    width: 30%;
    height: auto;
    padding: 30px;
    border-radius: 10px;
    position: relative;
    display: flex;
    justify-content: center;
    color: var(--eggshell);
}

.icon{
    background-color: var(--dark-blue);
    width: 100px;
    height: 100px;
    position: absolute;
    left: 40%;
    top: -50px;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.icon img{
    height: 70px;
}

.login_container form{
    display: flex;
    flex-direction: column;
    width: 70%;
}

.label{
    margin-top: 20px;
    color: var(--eggshell);
}

.field{
    margin-top: 5px;
    background-color: transparent;
    height: 30px;
    border: 1px solid var(--eggshell);
    border-radius: 3px;
    outline: none;
    padding-left: 5px;
    color: var(--eggshell);
    font-family: inherit;
}

.login_container button{
    margin: 20px 0px;
    height: 40px;
    width: 100%;
    background-color: var(--bg);
    border: 0px;
    border-radius: 3px;
    color: var(--dark-blue);
    font-size: inherit;
    font-weight: bold;
    cursor: pointer;
    font-family: inherit;
    text-transform: uppercase;
}

.fa-globe-africa{
    color: var(--bg);
    font-size: 70px;
}

.login_container button:hover{
    background-color: white;
}

#login-alerts{
    text-align: center;
    color: var(--vermilion);
    height: 20px;
}

#header{
    display: flex;
    justify-content: space-between;
}

#left-side{
    padding: 0.5% 4%;
    display: flex;
    align-items: center;
    width: 19%;
    justify-content: space-between;
}
#left-side i{
    font-size: 45px;
}
#left-side span{
    font-size: 25px;
    cursor: pointer;
}

#right-side{
    padding: 0.5% 4%;
    display: flex;
    align-items: center;
    width: 30%;
    justify-content: space-between;
    /* background-color: green; */
}

#right-side i{
    font-size: 30px;
    /* color: #CD4631; */
}
#right-side span{
    font-size: 15px;
    max-width: 90%;
    text-overflow: ellipsis;
    overflow: hidden;
}

.clouds-container{
    padding: 2% 4%;
    background-color: var(--bg);
    display: flex;
    flex-direction: column;
    align-items: center;
    -moz-box-shadow:    inset 0 1px 0 var(--eggshell);
    -webkit-box-shadow: inset 0 1px 0 var(--eggshell);
    box-shadow:         inset 0 1px 0 var(--eggshell);
    min-height: 90vh;
}

.loading{
    background-color: var(--bg);
    display: flex;
    align-items: center;
    justify-content: center;
    min-height: 90vh;
}

#empty-list{
    width: 100%;
    height: 50vh;
    display: flex;
    align-items: center;
    justify-content: center;
}

.viewer-container{
    height: 90vh;
}

#search-bar{
    position: relative;
    margin: 20px;    
}

.search-input{
    font-family: inherit;
    height: 40px;
    width: 250px;
    outline: none;
    border: 1px solid var(--eggshell);
    padding-left: 50px;
    font-size: 13px;
}

#search-bar i{
    position: absolute;
    top: 25%;
    left: 5%;
    font-size: 20px;
}

.list-title{
    color: var(--brown-sugar);
}

#clouds-list{
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    width: 80%;
}

.item-img{
    display: flex;
    justify-content: center;
    align-items: center;
    height: 70%;
    width: 200px;
    background-color: white;
}

.item-img img{
    width: 140px;
    height: 140px;
    border-radius: 50%;
}

.item-img i{
    position: absolute;
    top: 2px;
    right: 10px;
    font-size: 25px;
}



#img{
    width: 140px;
    height: 140px;
    background-repeat: no-repeat;
    background-size: 120%;
    background-position: center;
    border-radius: 50%;
    border: 1px solid var(--tumbleweed);
}

.card-details{
    display: flex;
    flex-direction: column;
    align-items: center;
}

.add-item-card{
    display: flex;
    margin: 20px;
    height: 300px;
    width: 200px;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-color: white;
    position: relative;
}
.add-item-card i{
    transition: all 0.3s ease;
    font-size: 90px;
}

.fa-plus-circle:hover{
    transform: scale(1.3);
}    

.fa-times{
    color: var(--vermilion);
}

.fa-times:hover{
    transform: scale(1.6);
    transition: transform 0.2s ease;
}

i{
    cursor: pointer;
    color: var(--dark-blue);
}

.add-item{
    position: absolute;
    top: 0px;
    left: 0px;
    visibility: hidden;
    width: 100%;
    height: 0px;
    background-color: white;
    border: 1px solid var(--gray);
    transition: all 0.08s ease;
}

.show-add-item-form{
    visibility: visible;
    height: 300px;
}

.add-item form{
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    height: 100%;
    width: 100%;
    background-image: linear-gradient( 173deg, #ECE9E6 10%, #FFFFFF 100%);
}

.img-insertion{
    background-position: center;
    background-size: cover;
    margin: 12px;
    height: 150px;
    width: 150px;
    border: 2px dotted #9E6240;
    border-radius: 50%;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
}

.img-insertion span{
    position: absolute;
    color: var(--brown-sugar);
}

.img-insertion input{
    width: 100%;
    height: 100%;
    opacity: 0;
    cursor: pointer;
}

.info-insertion{
    padding: 0px 15px;
}

.info-insertion label{
    font-size: 14px;
}

.input_field{
    margin-top: 5px;
    font-family: inherit;
    border: none;
    outline: none;
    border-bottom: 2px solid var(--gray);
    font-size: 13px;
    width: 95%;
    background-color: transparent;
}

.button-div{
    display: flex;
    align-items: center;
    width: 100%;
    justify-content: center;
    height: 100%;
}

.button-div button{
    height: 40px;
    width: 80%;
    font-family: inherit;
    font-size: 14px;
    border: none;
    border-radius: 3px;
    background-color: var(--pewter-blue);
    cursor: pointer;
    color: white;
}

.button-div i{
    font-size: 45px;
    color: var(--vermilion);
    margin-left: 5px;
    transition: all 0.1s ease;
}

.date{
    font-size: 13px;
    font-style: italic;
}

.cloud-item{
    background-color: transparent;
    perspective: 1000px;
    width: 200px;
    height: 300px;
    margin: 20px;
}

.cloud-inner{
    position: relative;
    text-align: center;
    transition: transform 0.8s;
    transform-style: preserve-3d;
    width: 100%;
    height: 100%;
}

.cloud-item:hover .cloud-inner {
    transform: rotateY(180deg);
}

.cloud-front, .cloud-back{
    position: absolute;
    -webkit-backface-visibility: hidden;
    backface-visibility: hidden;
    width: 100%;
    height: 100%;
}

.cloud-front{
    border: 1px solid var(--eggshell);
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: var(--gray);
    cursor: pointer;
}

.cloud-back{
    transform: rotateY(180deg);
    border: 1px solid var(--eggshell);
    background-image: linear-gradient( 173deg, #ECE9E6 10%, #FFFFFF 100%);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    /* background-color: white; */
}

.cloud-options{
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 80%;
    height: 50%;
    justify-content: space-evenly;
}

.cloud-option{
    cursor: pointer;
    width: 70%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    font-size: 18px;
}

.option{
    width: 70%;
}

.option-icon{
    width: 30%;
}

.cloud-options i{
    font-size: 22px;
}

.edit-cloud-form{
    display: flex;
    justify-content: space-evenly;
}

.edit-cloud-name{
    border: none;
    outline: none;
    border-bottom: 1px solid var(--dark-blue);
    font-family: inherit;
}

.back-link{
    text-decoration: none;
    color: var(--dark-blue);
}

.back-link:hover > .fa-arrow-circle-left{
    transform: translateX(-3px);
}

.fa-arrow-circle-left{
    color: inherit;
    transition: transform 0.1s ease;
}

.projects-list{
    margin: 20px;
    margin-top: 0px;
    width: 80%;
    display: flex;
    justify-content: stretch;
    flex-wrap: wrap;
}

.project-card{
    display: flex;
    width: 30%;
    justify-content: space-evenly;
    align-items: center;
    height: 100px;
    margin: 1.5%;
    border-radius: 5px;
    border: 1px solid var(--pewter-blue);
    background-color: white;
    cursor: pointer;
    transition: all 20ms ease;  
}

.project-card:hover{
    transform: scale(1.1);
}

.project-infos{
    width: 40%;
    display: flex;
    justify-content: center;
}

.project-options{
    width: 20%;
    display: flex;
    justify-content: space-between;
}

.project-options i{
    font-size: 20px;
    transition: all 0.2s ease-in;
}

.project-options i:hover{
    transform: scale(1.08);
}

.fa-trash{
    color: var(--vermilion);
}

#menu{
    margin: 20px; 
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 80%;
}

.add-project{
    position: relative;
    height: 30px;
    width: 20%;
    
}

.add-project-button{
    font-family: inherit;
    font-size: 14px;
    border: 1px solid --dark-blue;
    border-radius: 3px;
    cursor: pointer;
    color: white;
    width: 100%;
    height: 100%;
    background-color: var(--dark-blue);
    transition: transform 0.2s ease;
}

.add-project button i {
    color: var(--eggshell);
    font-size: 13px;
}

.add-form{
    visibility: hidden;
    position: absolute;
    top: 0px;
    border-radius: 3px;
    width: 0%;
    height: 100%;
    transition: all 0.1s ease-in;
    display: flex;
}

.add-form input{
    font-family: inherit;
    height: 90%;
    width: 100%;
    padding-left: 8px;
    outline: none;
    border: 1px solid var(--eggshell);
    font-size: 13px;
}

.show-add-project-form{
    visibility: visible;
    width: 100%;
}

.add-form button{
    background-color: white;
    /* border: 1px solid var(--eggshell);
    width: 40px; */
}

.add-form button i{
    color: var(--dark-blue);
}

#search-projects{
    position: relative;
}

#search-projects i{
    position: absolute;
    top: 25%;
    left: 5%;
    font-size: 20px;
    color: var(--dark-blue);
}

#models-list{
    display: flex;
    align-items: center;
    justify-content: stretch;
    flex-wrap: wrap;
    width: 80%;
}

.model{
    width: 44%;
    height: 150px;
    margin: 20px;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    border-radius: 5px;
    transition: transform 9ms ease;
    border: 1px solid var(--brown-sugar);
    background-image: linear-gradient( 173deg, #ECE9E6 10%, #FFFFFF 100%);
}

.model i:hover{
    transform: scale(1.2);
}

.model-left{
    width: 40%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    overflow: hidden;
}

#loading-map{
    width: 30px;
    height: auto;
}

.model-right{
    display: flex;
    width: 50%;
    height: 60%;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
}

.model-name{
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    height: 100%;
    width: 100%;
}

.model-options{
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    height: 100%;
    width: 60%;
}

.model-options i{
    font-size: 25px;
}

.modal{
    position: fixed;
    left: 0;
    top:0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.5);
    display: flex;
    align-items: center;
    justify-content: center;
    opacity: 0;
    transition: all 0.2s ease-in-out;
    pointer-events: none;
    z-index: 1;
}

.modal.show{
    opacity: 1;
    pointer-events: visible;
}

.modal-content{
    width: 400px;
    background-color: white;
    transform: translateY(-200px);
    transition: all 0.2s ease-in-out;
}

.modal.show .modal-content{
    transform: translateY(0);
}

.modal-header, .modal-footer{
    padding: 10px;
}

.modal-header h4, span{
    margin: 0px;
    margin-right: 5px;
}

.modal-header{
    display: flex;
    align-items: center;
}

.modal-header i{
    font-size: 20px;
}

.modal-body{
    padding: 10px;
    border-top: 1px solid #eeeeee;
    border-bottom: 1px solid #eee;
}

.modal-footer{
    display: flex;
    justify-content: center;
}

.modal-footer button{
    margin-left: 5px;
    font-family: inherit;
    height: 30px;
    width: 80px;
    cursor: pointer;
    border: none;
    border-radius: 2px;
    color: white;
}

.cancel-ifc{
    background-color: var(--vermilion);
}

.submit-ifc{
    background-color: var(--dark-blue);
}

.add-ifc-form{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
    height: 300px;
    /* background-color: mediumturquoise; */
}

.file-input{
    display: flex;
    justify-content: center;
    align-items: center;
    height: 80%;
    width: 80%;
    border: dashed 2px var(--dark-blue);
    box-sizing: border-box;
    background-color: mintcream;
    position: relative;
}

.file-input input{
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 100%;
    opacity: 0;
    cursor: pointer;
}

.filename-input{
    display: flex;
    justify-content: center;
    width: 70%;
}

.filename-input input{
    outline: none;
    border: none;
    border-bottom: solid 1px var(--dark-blue);
    width: 50%;
    color: var(--dark-blue);
    font-family: inherit;
    padding-left: 2px;
    
}

.fa-check-circle{
    font-size: 100px;
    color: rgb(123, 167, 80);
}

